.Toolbar {
    height: 56px;
    width: 100vw;
    max-width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #e7e7e7;
    display: flex;
    /* justify-content: space-between; */
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 90;
    background-image: -webkit-linear-gradient(20deg, #e7e7e7 50%, rgba(0, 0, 0, 0) 0%);
    BACKGROUND-COLOR: WHITE;
    -webkit-transition: top 0.4s;
    transition: top 0.4s;
}

.Toolbar-hidden {
    top: -56px !important;
}

.Toolbar nav {
    height: 100%;
}

.Logo {
    position: absolute;
    right: 0;
    margin-right: 20px;
    height: 50px;
}

@media (max-width: 800px) {
    .DesktopOnly {
        display: none;
    }
}
.Logo {
    /* background-color: white; */
    /* padding: 8px; */
    /* height: 100%; */
    box-sizing: border-box;
    border-radius: 5px;
}

.Logo img {
    height: 100%;
}
.NavigationItems {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-flow: column;
    align-items: center;
    height: 100%;
}

@media (min-width: 800px) {
    .NavigationItems {
        flex-flow: row;
    }
}
.NavigationItem {
    margin: 10px 0;
    box-sizing: border-box;
    display: block;
    width: 100%;
}

.NavigationItem a {
    color: black;
    text-decoration: none;
    width: 100%;
    box-sizing: border-box;
    display: block;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
    background-color: #e7e7e7;
    border-bottom: 4px solid #666666;
    color: black;
}

@media (min-width: 800px) {
    .NavigationItem {
        margin: 0;
        display: flex;
        height: 100%;
        width: auto;
        align-items: center;
    }
    
    .NavigationItem a {
        color: black;
        height: 100%;
        padding: 16px 10px;
        border-bottom: 4px solid transparent;
    }
    
    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active {
        background-color: #e7e7e7;
        border-bottom: 4px solid #666666;
        color: black;
    }
}
.DrawerToggle {
    width: 40px;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: space-around;
    align-items: center;
    padding: 10px 0;
    box-sizing: border-box;
    cursor: pointer;
}

.DrawerToggle div {
    width: 90%;
    height: 3px;
    background-color: black;
}

@media (min-width: 801px) {
    .DrawerToggle {
        display: none;
    }
}
.SideDrawer {
    position: fixed;
    width: 280px;
    max-width: 70%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    background-color: #e7e7e7;
    padding: 32px 16px;
    box-sizing: border-box;
  }

.initial-close{
    display: none;
}

/* Safari 4.0 - 8.0 */
@-webkit-keyframes open-sidebar {
    from {
        left: -100%;
    }      
    to{
        left: 0%;      
    }
}

/* Standard syntax */
@keyframes open-sidebar {
    from {
        left: -100%;
    }      
    to{
        left: 0%;   
    }
}

/* Safari 4.0 - 8.0 */
@-webkit-keyframes close-sidebar {
    from {
        left: 280px;
    }      
    to{
        left: 0px;    
    }
}

/* Standard syntax */
@keyframes close-sidebar {
    from {
        left: 280px;
    }      
    to{
        left: 0px;     
    }
}

@media (min-width: 800px) {
    .SideDrawer {
        display: none;
    }
}

.Open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    -webkit-animation-name: open-sidebar; /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 0.5s; /* Safari 4.0 - 8.0 */
    animation-name: open-sidebar;
    animation-duration: 0.5s;    
}

.Close {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-animation-name: close-sidebar; /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 0.5s; /* Safari 4.0 - 8.0 */
    animation-name: close-sidebar;
    animation-duration: 0.5s;     
}

